<template>

  <ul class="cms-submenu" v-if="$route.path !== '/index'">
    <template v-for="(item,index) in menuInfo">
      <div class="cms-submenu-menu" v-if="item.isShow && item.isShowChildLayout&&item.children.length>0" :key="index">
        <span class="cms-submenu-title" :class='currentChildPath === child.path ? "active" : "" ' :data-path='child.path'>
          {{child.name}}</span>
        <el-collapse-transition>
          <ul v-show="currentChildPath===child.path">
            <template v-for="(son,index2) in child.children">
               <li v-if="son.isShow" class="cms-submenu-item cms-submenu-child"   :key="index2" :data-path='son.path' >
              <router-link :to="son.path" :target='son.isBlank?"_blank":""'>{{son.name}}</router-link>
            </li>
            </template>
          </ul>
        </el-collapse-transition>
      </div>
      <li class="cms-submenu-item" v-else-if='item.isShow' :key="item.path" :class="[activePath(item.path)?'active':'']">
        <router-link :to="item.path" :style="{color:activePath(item.path)?color:''}">{{item.name}}</router-link>
      </li>

    </template>

  </ul>
</template>
<script>
export default {
  data() {
    return { }
  },
  computed: {
    menuInfo() {
      return this.$store.state.sideBar.childMenu
    },
    activeMenus() {
      return this.$store.state.sideBar.activeMenus
    },
    color: {
      get() {
        return this.$store.state.system.color
      },
      set() {}
    }
  },
  methods: {
    activePath(path) { // 判断路径是否存在
      return this.activeMenus.indexOf(path) > -1
    }
  },
  created() {

  },
  watch: {
    $route: {
      handler() {
        if (this.$route.path === '/index') {
          this.$store.dispatch('setActiveMenus', ['/'])
        } else {
          const ansycMap = this.$store.state.permissions.ansyRouterMap
          const rootPath = this.$route.matched[0].path
          const activeMenus = this.$route.matched.map(v => v.path)
          const childItem = ansycMap.filter(v => v.path === rootPath)
          this.$store.dispatch('setChildInfo', childItem[0].children)
          this.$store.dispatch('setActiveMenus', activeMenus)
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang='scss' scoped>
</style>



