<template>
  <div class="cms-layout">
    <cms-header></cms-header>
    <div class="main-cnt">
      <div class="page-component__scroll" id="layout" ref="elscrollbar">
        <div class="page-container">
          <el-scrollbar class="page-component__nav">
            <cms-side-bar></cms-side-bar>
          </el-scrollbar>
          <router-view class="cms-body"  :style="{marginLeft:$route.path === '/index' ? 0 : ''}"></router-view>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import cmsHeader from './header'
import cmsSideBar from './sideBar'
export default {
  components: {
    cmsHeader,
    cmsSideBar
  },
  computed: {},
  data() {
    return {}
  }
}
</script>
<style lang="scss" scoped>
.page-component__nav {
  width: 190px;
  position: fixed;
  top: 0;
  bottom: 0;
  margin-top: 94px;
  transition: padding-top 0.3s;
}
</style>






